<template>
  <div class="ManageStaff">
    <el-container>
      <el-header style="height: 40px;">
        <div class="top-menu">
          <li @click="routerGo('/user/BasicInfo')"></li>
          <li @click="routerGo('/user/ManageStaff')" class="is-active"></li>
          <li @click="routerGo('/user/ManageRole')" ></li>
        </div>
      </el-header>
      <el-main>
        <el-row class="row-bg">
          <el-col :span="24">
            <div class="main-header">
              <div class="left-operation">
                <!--  -->
                <el-select v-model="selectStatusValue" placeholder="" size="small" class="selectbox" @change="selectStatus">
                  <el-option v-for="item in optionsStatus"
                    :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>

                <el-select v-model="selectSoleValue" placeholder="" size="small" class="selectbox">
                  <el-option v-for="item in roleOptions"
                    :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <!--  -->
                <el-input v-model="queryNameValue" @change="querySearchName" size="small"
                  placeholder="" suffix-icon="el-icon-search" style="width: 200px;"></el-input>

                <el-input v-model="queryMobileValue" @change="querySearchMobile" size="small"
                  placeholder="" suffix-icon="el-icon-search" style="width: 200px;"></el-input>
              </div>
              <div class="right-operation">
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row class="row-bg">
          <el-col :span="24">
            <div class="button-box">
              <el-button size="small" @click="addStaffDialog()"></el-button>
              <el-button size="small" @click="changeAbleToTrue"></el-button>
              <el-button size="small"@click="changeAbleToFalse"></el-button>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row  class="row-bg">
          <div class="table-data-area">
            <el-table :data="tableData" size="small" header-row-class-name="table-title"
            @selection-change="handleSelectionChange">
             <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column prop="username" label=""></el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="mobile" label=""></el-table-column>
              <el-table-column prop="roleName" label=""></el-table-column>
              <el-table-column prop="registerTime" label=""></el-table-column>
              <!--  -->
              <el-table-column label="">
                <template slot-scope="scope">
                  <span v-if="scope.row.able == true"></span>
                  <span v-else></span>
                </template>
              </el-table-column>
              <!--  -->
              <el-table-column label="" width="250px">
                <template slot-scope="scope" >
                  <el-button size="mini" @click="addStaffDialog(scope.row)"></el-button>
                  <!--  -->
                  <el-button size="mini" @click="enableStaff(scope.row)">
                    <span v-if="scope.row.able == true"></span>
                    <span v-else></span>
                  </el-button>

                  <el-button type="danger" size="mini" @click="deleteStaff(scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-row>
        <el-row class="row-bg footer-row">
          <el-col :span="24">
            <div class="left-export-button">
              <el-button size="small"></el-button>
            </div>
            <div class="current-page-area">
              <el-pagination
                class="right"
                background :current-page="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :total="total">
              </el-pagination>
            </div>
          </el-col>
        </el-row>


        <!--  -->
        <el-dialog title="" :visible.sync="addStaff" :before-close="closeDiolog">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane  label="" name="first">
              <el-form :model="form">
                <el-form-item label=""  >
                  <el-input v-model="form.mobile" auto-complete="off" :disabled="updateStaff"></el-input>
                </el-form-item>
                <el-form-item label="" >
                  <el-input v-model="form.name" auto-complete="off" :disabled="updateStaff"></el-input>
                </el-form-item>
                <el-form-item v-if="updateStaff === false" label="" >
                  <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" >
                  <el-checkbox-group v-model="checkedItem">
                   <el-checkbox v-for="role in roleItem" :label="role.id" :key="role.id">{{role.name}}</el-checkbox>
                 </el-checkbox-group>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane v-if="updateStaff === false" label="" name="second">
              <el-form :model="form">
                <span></span>
                <el-input v-model="mobileQuery" placeholder="" style="width: 300px;"></el-input>
                <el-button></el-button>
                <p>{{mobileQuery}}</p>
                <el-form-item label="">
                  <el-checkbox-group v-model="form.type">
                   <el-checkbox label="" name="role"></el-checkbox>
                 </el-checkbox-group>
                </el-form-item>
              </el-form>
            </el-tab-pane>
          </el-tabs>
          <div slot="footer" class="dialog-footer">
            <el-button @click="closeDiolog"> </el-button>
            <el-button type="primary" @click="commitUpdateSoff"> </el-button>
          </div>
        </el-dialog>



        <el-dialog title="" :visible.sync="removeStaff">
          <p></p>
          <p>{{deleteStaffValue.name}}</p>
          <p>{{deleteStaffValue.mobile}}</p>
          <div slot="footer" class="dialog-footer">
            <el-button @click="closeDiolog"> </el-button>
            <el-button type="primary" @click="commitDeleteStaff"> </el-button>
          </div>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import {callApiToken, changeDateFormat} from '@/data/callApi'
export default {
  name: 'ManageStaff',
  data () {
    return {
      activeName: 'first',
      status: '',

      form: {
        username: '',
        name: '',
        mobile: '',
        password: '',
        roleIds: []
      },
      checkedItem: [],
      oldRoleIds: [],
      roleItem: [],

      selectedTable: [],
      deleteStaffValue: {},

      mobileQuery: '',
      activeIndex: '/managestaff',

      selectSoleValue: '',
      selectStatusValue: '',

      queryNameValue: '',
      queryMobileValue: '',

      removeStaff: false,
      addStaff: false,
      updateStaff: false,

      currentPage: 1,
      total: 0,

      dateValue: '',
      optionsStatus: [
        {label: '', value: ''},
        {label: '', value: false},
        {label: '', value: true}
      ],
      roleOptions: [
        {label: '', value: '0'}
      ],
      selectValue: '',
      tableData: [
        {Id: '1', userName: 'Shadow', reallyName: 'XXX', mobilePhone: '192...123123', roleName: '', createTime: '2018-01-01 00:00:00', status: ''},
        {Id: '2', userName: 'Shadow', reallyName: 'YYY', mobilePhone: '192...123123', roleName: '', createTime: '2018-01-01 00:00:00', status: ''}
      ]
    }
  },
  methods: {
    // 
    querySearchName () {
      console.log(this.queryNameValue)
      callApiToken('/user/load_user_infos', {able: this.selectStatusValue, mobile: this.queryMobileValue, username: this.queryNameValue, page: 1, pageSize: 10}, this.updateTableData)
    },
    // 
    querySearchMobile () {
      console.log(this.queryMobileValue)
      callApiToken('/user/load_user_infos', {able: this.selectStatusValue, mobile: this.queryMobileValue, name: this.queryNameValue, page: 1, pageSize: 10}, this.updateTableData)
    },
    // 
    selectStatus () {
      callApiToken('/user/load_user_infos', {able: this.selectStatusValue, mobile: this.queryMobileValue, name: this.queryNameValue, page: 1, pageSize: 10}, function (res) {
        console.log(res)
      })
    },
    // 
    handleCurrentChange (value) {
      console.log(value)
      callApiToken('/user/load_user_infos', {able: this.selectStatusValue, mobile: this.queryMobileValue, name: this.queryNameValue, page: value, pageSize: 10}, this.updateTableData)
    },
    handleAvatarSuccess () {

    },
    // 
    changeAbleToTrue () {
      var temp = []
      for (var i = 0; i < this.selectedTable.length; i++) {
        temp.push(this.selectedTable[i].id)
      }
      console.log(temp)
      callApiToken('')
    },
    // 
    changeAbleToFalse () {
      var temp = []
      for (var i = 0; i < this.selectedTable.length; i++) {
        temp.push(this.selectedTable[i].id)
      }
      console.log(temp)
    },
    // 
    handleSelectionChange (value) {
      this.selectedTable = value
    },
    handleClick () {

    },
    routerGo (url) {
      this.$router.push({path: url})
    },
    // 
    addStaffDialog (rowValue) {
      let me = this
      if (rowValue) {
        // 
        me.checkedItem = []
        me.oldRoleIds = []
        this.form.id = rowValue.id
        this.form.username = rowValue.username
        this.form.mobile = rowValue.mobile
        this.form.name = rowValue.name
        this.updateStaff = true
        callApiToken('/role/get_user_roles', {userId: rowValue.id}, function (res) {
          if (res.status >= 200 && res.status < 300) {
            if (res.data.success) {
              for (var i = 0; i < res.data.content.length; i++) {
                me.checkedItem.push(res.data.content[i].roleId)
                me.oldRoleIds.push(res.data.content[i].roleId)
              }
            }
          }
        })
      } else {
        // 
        this.updateStaff = false
      }
      this.activeName='first'
      this.addStaff = true
    },

    // 
    commitUpdateSoff () {
      this.form.username = this.form.mobile
      let me = this
      for (var i = 0; i < this.checkedItem.length; i++) {
        this.form.roleIds.push(this.checkedItem[i])
      }
      if (this.updateStaff) {
        console.log(this.form.id)
        callApiToken('/role/save_user_role', {userId: this.form.id, oldRoleIds: me.oldRoleIds, newRoleIds: me.checkedItem} ,function (res) {
          if (res.status >= 200 && res.status < 300) {
            if (res.data.success) {
              me.$message({message: '', type: 'success'})
              me.closeDiolog()
              return
            }
          }
          console.log(res)
        })
      } else {
        callApiToken('/user/add_user', this.form, function (res) {
          if (res.status >= 200 && res.status <300) {
            if (res.data.success) {
              callApiToken('/user/load_user_infos',{page: 1, pageSize: 10}, me.updateTableData)
              me.$message({message: '', type: 'success'})
              me.closeDiolog()
              return
            }
          }
          console.log(res)
          me.closeDiolog()
        })
      }
    },

    // 
    enableStaff (row) {
      let me = this
      console.log(row.id)
      if (row.able) {
        callApiToken('/user/lock_user', {userId: row.id}, function (res) {
          if(res.status >= 200 && res.status < 300) {
            if (res.data.success) {
              me.$message({message: row.name + ':'+ row.mobile + ' ' +'', type: 'success'})
              row.able = !row.able
              return
            }
            me.$message.error(res.data.errmsg)
            return
          }
          me.$message.error(res.status)
          console.log(res)
        })
      } else {
        callApiToken('/user/unlock_user', {userId: row.id}, function (res) {
          if(res.status >= 200 && res.status < 300) {
            if (res.data.success) {
              me.$message({message: row.name + ':'+ row.mobile + ' ' +'', type: 'success'})
              row.able = !row.able
              return
            }
            me.$message.error(res.data.errmsg)
            return
          }
          me.$message.error(res.status)
          console.log(res)
        })
      }
    },

    // 
    deleteStaff (row) {
      this.deleteStaffValue = row
      this.removeStaff = true
    },

    // 
    commitDeleteStaff () {
        let me = this
      callApiToken('/user/delete_user', {userIds: [this.deleteStaffValue.id]}, function (res) {
        if (res.status >= 200 && res.status < 300) {
          if (res.data.success) {
            me.$message({message: '', type: 'success'})
            callApiToken('/user/load_user_infos', {mobile: me.queryMobileValue, name: me.queryNameValue, page: 1, pageSize: 10}, me.updateTableData)
            return
          }
        }
        console.log(res)
      })
      this.removeStaff = false
    },

    // 
    closeDiolog () {
      this.removeStaff = false
      this.addStaff = false
      this.form.id = ''
      this.form.username = ''
      this.form.name = '',
      this.form.mobile = '',
      this.form.password = '',
      this.form.roleIds = []
      this.checkedItem = []
    },

    // 
    updateTableData (res) {
      let me = this
      console.log(res)
      if (res.status >= 200 && res.status < 300) {
        if (res.data.success) {
          me.tableData = res.data.content.records
          me.total = res.data.content.total
          me.currentPage = res.data.content.page
          for (var i = 0; i < me.tableData.length; i++) {
            // callApiToken('/role/get_user_roles', {userId: })
            me.tableData[i].registerTime = changeDateFormat(me.tableData[i].registerTime)
          }
        }
      }
    }
  },
// 
  created: function () {
    let me = this
    callApiToken('/user/load_user_infos',{page: 1, pageSize: 10}, this.updateTableData)

    callApiToken('/role/query_role', {applicationId: 1, page:1, pageSize: 100}, function (res) {
      me.roleItem = res.data.content.records
    })
  }
}
</script>

<style scoped lang='scss'>
  .ManageStaff {
    .top-menu {
      height: 38px;
      li {
        background-color: white;
        border: 1px solid #d4d4d4;
        height: 38px;
        width: 110px;
        float: left;
        line-height: 38px;
        text-align: center;
        list-style-type:none;
      }
      .is-active {
        height: 36px;
        line-height: 43px;
        border-top: 3px solid #205081;
        border-bottom: 1px solid white;
      }
    }
    .el-main {
      padding-top: 0px;
      .row-bg {
        border-left: 1px solid #D7D7D7;
        border-right: 1px solid #D7D7D7;
        background-color: white;
        .main-header {
          padding-top: 20px;
          padding-left: 40px;
          padding-right: 40px;
          .left-operation {
            .datebox {
              width: 230px;
            }
            .selectbox {
              width: 120px;
            }
            float: left;
          }
        }
        .table-data-area {
          padding-top: 20px;
          padding-right: 40px;
          padding-left: 40px;
        }
        .button-box {
          padding-top: 20px;
          padding-left:40px;
        }
        .left-export-button {
          padding-top: 20px;
          padding-left: 40px;
          float: left;
          padding-bottom: 10px;
        }
        .current-page-area {
          padding-top: 20px;
          padding-right: 40px;
          float: right;
        }
      }
      .el-form {
        padding-top: 20px;
        padding-left: 20px;
        .el-form-item {
          .el-input {
            width: 200px;
          }
        }
      }
    }
    .footer-row {
      border-bottom: 1px solid #d7d7d7;
    }
  }
</style>



